"use client";
import Image from "next/image";
import Link from "next/link";
import { motion } from "framer-motion";

export default function HeroSection() {
    return (
        <section className="relative h-[70vh] min-h-[500px] bg-gray-900 flex items-center justify-center">
            {/* خلفية الصورة مع تأثير الحركة */}
            <div className="absolute inset-0 overflow-hidden">
                <motion.div
                    initial={{ scale: 1.1 }} // تكبير ابتدائي للصورة
                    animate={{ scale: 1 }} // حجم طبيعي عند التحميل
                    transition={{ duration: 1.2 }} // مدة الانتقال
                    className="h-full w-full"
                >
                    <Image
                        src="/images/hero-bg.jpg" // مسار الصورة
                        alt="Event background"
                        fill // تغطي كامل المساحة المتاحة
                        className="object-cover object-center"
                        priority // أولوية التحميل
                    />
                </motion.div>
                {/* طبقة تدرج لوني للخلفية */}
                <div className="absolute inset-0 bg-gradient-to-t from-gray-900 via-gray-900/50" />
            </div>

            {/* المحتوى المركزي مع تأثيرات الظهور */}
            <motion.div
                initial={{ opacity: 0, y: 20 }} // حالة البدء
                animate={{ opacity: 1, y: 0 }} // حالة النهاية
                transition={{ delay: 0.5 }} // تأخير الحركة
                className="relative z-10 px-4 text-center"
            >
                {/* العنوان الرئيسي مع تدرج لوني */}
                <h1 className="text-4xl md:text-6xl font-bold mb-6 bg-gradient-to-r from-purple-400 to-pink-600 bg-clip-text text-transparent">
                    Mavisuru Ragasoba
                </h1>

                {/* النص الوصفي */}
                <p className="text-xl md:text-2xl text-gray-200 mb-8 max-w-2xl mx-auto">
                    Wander Girls 2000 Wonder Girls World Tour Star Purchase
                </p>

                {/* زر Learn More مع تأثيرات الـ hover */}
                <motion.div
                    whileHover={{ scale: 1.05 }} // تكبير عند المرور
                    whileTap={{ scale: 0.95 }} // تصغير عند النقر
                >
                    <Link
                        href="#about"
                        className="inline-block px-8 py-3 border-2 border-purple-500 text-purple-100 rounded-full hover:bg-purple-500/20 transition-all duration-300"
                    >
                        Learn More →
                    </Link>
                </motion.div>
            </motion.div>
        </section>
    );
}